/*GLOBAL*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #f5f5f5;
  font-size: 14px;
  line-height: 18px;
  color: #0f0f0f;
}

img {
  max-width: 100%;
  height: auto;
}

.remove {
  transition-duration: 0.5s;
  transform: translateX(-300px);
  opacity: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/*BLOCKS*/
.button {
  display: inline-block;
  padding: 10px 15px;
  color: #ffe4c2;
  border: none;
  min-width: 100px;
  text-decoration: none;
  text-align: center;
  font-size: 14px;
  background-color: #ff8c00;
  border-radius: 5px;
  transition-duration: 0.1s;
  cursor: pointer;
}

.button:hover {
  transform: translateY(-2px);
  box-shadow: 0px 2px #ac5e00;
}

.button:focus {
  transform: translateY(-2px);
  box-shadow: 0px 2px #ac5e00;
}

.button:active {
  transform: none;
  box-shadow: none;
}

.container {
  margin: 0 15px;
}

.input {
  padding: 10px;
  font-size: 14px;
  line-height: 18px;
  width: 100%;
  border: none;
  border-radius: 5px;
}

.auth {
  margin: auto;
}

.auth__container {
  max-width: 370px;
}
@media (min-width: 400px) {
  .auth__container {
    margin: 0 auto;
  }
}

.auth__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.auth-form__container {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  gap: 10px;
}

.auth-form__container--error {
  justify-self: center;
  color: #b60000;
}

.auth-form__field {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.auth-form__title {
  font-size: 16px;
  line-height: 1;
  color: #818181;
}

.auth-form__buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.page-header__container {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: aqua;
}

.page-header__bar {
  padding: 10px;
  background-color: aquamarine;
  border-radius: 5px;
}

.logo {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 72px;
  align-items: center;
  text-decoration: none;
  color: #665959;
}

.logo__title {
  margin: 0;
  font-size: 16px;
  text-transform: uppercase;
  word-break: break-all;
}

.nav__button {
  font-weight: 700;
  text-transform: uppercase;
}

.page-main {
  display: flex;
  min-height: 100vh;
}

.page-main__section {
  min-width: 100%;
}

.main-menu {
  background-color: #ebebeb;
}

.main-menu__container {
  padding: 15px 0;
}

.main-menu__list {
  margin: 0;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style: none;
  background-color: #ff8c00;
  border-radius: 5px;
}

.main-menu__link {
  display: block;
  padding: 10px 12px;
  font-size: 18px;
  line-height: 20px;
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  background-color: #cf7200;
  border-radius: 5px;
  transition-duration: 0.1s;
}

.main-menu__link:hover {
  transform: translateY(-2px);
  box-shadow: 0px 2px #ac5e00;
}

.main-menu__link:focus {
  transform: translateY(-2px);
  box-shadow: 0px 2px #ac5e00;
}

.main-menu__link:active {
  transform: none;
  box-shadow: none;
}

.main-form {
  background-color: #ebebeb;
}

.main-form__container {
  padding: 15px 0;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.form__buttons {
  display: flex;
  justify-content: end;
  gap: 15px;
}

.field-group {
  position: relative;
  margin: 0;
  padding: 20px;
  background-color: #dcdcdc;
  border-radius: 5px;
  border: none;
}

.field-group__bar {
  display: grid;
  grid-template-columns: 35px 1fr 30px;
  min-height: 48px;
  align-items: center;
  background-color: #dcdcdc;
}

.field-group__title {
  margin-left: 20px;
  font-size: 20px;
  line-height: 24px;
}

.field-group__button--hidden {
  position: relative;
  padding: 5px;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}
.field-group__button--hidden::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 30px;
  height: 30px;
  background-image: url("../images/arrow.svg");
  background-size: 30px 30px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  transform: rotate(90deg);
  transition-duration: 0.5s;
}
.field-group__bar--close .field-group__button--hidden::before {
  transform: rotate(-90deg);
}
.field-group__button--remove {
  position: relative;
  justify-self: end;
  margin-left: auto;
  width: 30px;
  height: 30px;
  border: none;
  cursor: pointer;
  background-color: inherit;
}
.field-group__button--remove::before, .field-group__button--remove::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 25px;
  height: 3px;
  background-color: #b0b0b0;
  transition-duration: 0.3s;
  border-radius: 4px;
}
.field-group__button--remove::before {
  transform: rotate(-45deg);
}
.field-group__button--remove::after {
  transform: rotate(45deg);
}
.field-group__button--remove:hover::before, .field-group__button--remove:hover::after {
  background-color: #cc0000;
}

.field-group__list {
  margin: 0;
  padding: 0;
  margin-top: 20px;
  max-height: 100vh;
  list-style: none;
  display: grid;
  gap: 10px;
  overflow: hidden;
  transition-duration: 0.5s;
  opacity: 1;
}
.field-group__bar--close ~ .field-group__list {
  margin-top: 0;
  max-height: 0;
  opacity: 0;
  transition-duration: 0.5s;
}
.field-group__list--planning-form {
  grid-template-columns: 1fr 1fr;
}

.field-group__item--task {
  grid-column: 1/-1;
}

@media (max-width: 767px) {
  .field-group__item--date {
    grid-column: 1/-1;
  }
}

.field-group__input--description {
  min-height: 100px;
  resize: vertical;
}

